import React from "react"
import { injectIntl } from "gatsby-plugin-intl"
import PropTypes from "prop-types"
import { Helmet } from "react-helmet"

import GlobalStyle from "./GlobalStyles/GlobalStyles"
import Nav from "./Nav"
import MobileNav from "./MobileNav"
import Footer from "./Footer/Footer"
import FloatBtn from "./FloatBtn"
import Cookie from "./Cookie"

const windowGlobal = typeof window !== "undefined" && window.location.pathname

const isHomepage = windowGlobal === "/" ? true : false
const Layout = ({ children, intl }) => {
    return (
        <>
            <GlobalStyle />
            <Helmet>
                <meta
                    name="viewport"
                    content="width=device-width,initial-scale=1,maximum-scale=1, shrink-to-fit=no,user-scalable=no,minimal-ui,viewport-fit=cover"
                />
                <title>Home</title>
                <script
                    dangerouslySetInnerHTML={{
                        __html: `
                document.addEventListener("touchmove",
                function(e) {
                  e.preventDefault();
                }, { passive: false }
              );
`,
                    }}
                />
            </Helmet>
            <FloatBtn />
            <Nav />
            <MobileNav />
            {children}
            <Footer isHomepage={isHomepage} />
            <Cookie />
        </>
    )
}

Layout.propTypes = {
    children: PropTypes.node.isRequired,
}

export default injectIntl(Layout)
